'use strict';

/**
* Binomial distribution standard deviation.
*
* @module @stdlib/stats-base-dists-binomial-stdev
*
* @example
* var stdev = require( '@stdlib/stats-base-dists-binomial-stdev' );
*
* var v = stdev( 100, 0.1 );
* // returns 3.0
*
* v = stdev( 20, 0.5 );
* // returns ~2.236
*/

// MODULES //

var stdev = require( './stdev.js' );


// EXPORTS //

module.exports = stdev;
